import { getCurrentUser } from '@/lib/auth';
import { sql } from '@vercel/postgres';
import { NextRequest, NextResponse } from 'next/server';

export async function POST(request: NextRequest, { params }: { params: Promise<{ id: string }> }) {
  try {
    const user = await getCurrentUser();

    if (!user || user.role !== 'student') {
      return NextResponse.json({ success: false, error: 'Only students can take quizzes' }, { status: 403 });
    }

    const { id } = await params;

    const result = await sql`
      INSERT INTO quiz_attempts (quiz_id, student_id)
      VALUES (${id}, ${user.userId})
      RETURNING id, quiz_id, student_id, started_at
    `;

    const attempt = result.rows[0];

    // Get quiz to include time limit
    const quizResult = await sql`
      SELECT time_limit_minutes FROM quizzes WHERE id = ${id}
    `;

    return NextResponse.json({
      success: true,
      data: {
        attemptId: attempt.id,
        quizId: attempt.quiz_id,
        startedAt: attempt.started_at,
        timeLimitMinutes: quizResult.rows[0].time_limit_minutes,
      },
    }, { status: 201 });
  } catch (error) {
    return NextResponse.json({ success: false, error: 'Failed to start quiz' }, { status: 500 });
  }
}
